}

.section-1 {
  width: 100%;
  background-image: linear-gradient(74deg, #8a47db 40%, #1cb8c4 100%);

  @media (max-width: 1439px) {
    //background-image: linear-gradient(74deg, #8a47db 45%, #1cb8c4 100%);  
  }

  .header-top-banner {
    position: absolute;
    top: -20px;
    right: -15px;
    transition: all 0.3s ease;
    z-index: 10;

    img  {
      width: 277px;
      height: auto;

      @media (min-width: 1200px) {
        width: 327px;
      }
    }

    &:hover {
      transition: all 0.3s ease;
      top: -15px;
      right: -10px;
      filter: drop-shadow(-1px 2px 4px rgba(0, 0, 0, 0.5));
    }
  }
}

.hamburger {
  display: block;
  position: relative;
  width: 25px;
  height: 30px;
  //background-color: red;

    //font-size: 30px;
    //position: absolute;
    //padding: 0 0 5px 0;
    cursor: pointer;
    //right: 0;
    //margin-right: -30px;
    //margin-top: 13px;
    //top: 0;
    //color: #fff;
    //background-color: rgba(0,0,0,0);

  &:before {
      content: "";
      position: absolute;
      top: 3px;
      left: 0;
      width: 100%;
      height: 5px;
      background-color: #ffffff;
      box-shadow: 0 10px 0 0 #ffffff,
                  0 20px 0 0 #ffffff;
  }
}